import React from 'react';
import classNames from 'classnames';
import { SearchIcon } from '../../icons';

export interface SearchFieldProps extends React.ComponentPropsWithRef<'input'> {
  readonly id: string;
  readonly label: string;
}

export const SearchField: React.FC<SearchFieldProps> = React.forwardRef<
  HTMLInputElement,
  SearchFieldProps
>(({ children, id, label, className, ...props }, ref) => (
  <div className="relative">
    <label htmlFor={id} className="visually-hidden">
      {label}
    </label>
    <input
      ref={ref}
      id={id}
      type="text"
      className={classNames(
        'rounded-full py-3 px-6 outline-0 bg-form text-body1 pr-12 focus:outline-none focus:ring',
        className
      )}
      {...props}
    >
      {children}
    </input>
    <SearchIcon className="absolute right-3 h-full text-body2" />
  </div>
));

SearchField.displayName = 'SearchField';
